@import "scss/reset";
@import "scss/base";
@import "scss/nav";
@import "scss/footer";

@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
body {
  font-family: "Poppins", sans-serif;
  background: linear-gradient(312deg, #544a7d, #ffd452);
  background-size: 400% 400%;

  animation: back 5000s ease infinite;
}

@keyframes back {
  0% {
    background-position: 0% 13%;
  }
  50% {
    background-position: 100% 88%;
  }
  100% {
    background-position: 0% 13%;
  }
}

.windows {
  li {
    position: fixed;
    border-radius: 8px;
    background-color: #fdfdfd;
    box-shadow: rgba(0, 0, 0, 0.14) 0px 4px 24px 0px,
      rgba(0, 0, 0, 0.06) 0px 2px 12px 0px,
      rgba(64, 64, 76, 0.1) 0px 0px 0px 0.6px;
    min-width: 300px;
    min-height: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
    transition: background-color 0.3s ease;
    header {
      background: #e1e4eb;
      border-radius: 4px 4px 0px 0px;
      width: 100%;
      height: 40px;
      padding: 8px 15px;
      position: absolute;
      top: 0;
      left: 0;
      transition: background 0.3s ease;
      .control {
        width: 12px;
        height: 12px;
        margin-top: 8px;
        border-radius: 50%;
        &.close {
          background: #ff796f;
          border: 0.5px solid rgba(0, 0, 0, 0.16);
          margin-bottom: 5px;
          display: inline-block;
        }
        &.hide {
          background: #ffd171;
          border: 0.5px solid rgba(0, 0, 0, 0.16);
          display: inline-block;
          margin: 0 5px 5px 5px;
        }
        &.expand {
          background: #64c255;
          border: 0.5px solid rgba(0, 0, 0, 0.16);
          display: inline-block;
          margin-bottom: 5px;
        }
      }
      .onglet {
        position: absolute;
        left: 80px;
        right: 8px;
        top: 8px;
        bottom: 0px;
        display: inline-flex;
        background: #ffffff;
        border-radius: 8px 8px 0px 0px;
        transition: all 0.3s ease;
        .favicon {
          display: inline-flex;
          margin: 8px 8px 8px 12px;
          img {
            width: 16px;
            height: 16px;
          }
        }
        h2 {
          display: inline-flex;
          width: 100%;
          align-items: center;
          font-family: "Roboto", sans-serif;
          font-style: normal;
          font-weight: normal;
          font-size: 12px;
          line-height: 16px;
          letter-spacing: 0.02em;
          color: #4b4d52;
          text-transform: capitalize;
        }
        .close {
          margin: 13px;
          width: 16px;
          height: 16px;
          img {
            width: 6px;
            height: 6px;
          }
        }
      }
    }
  }
}
.is-grabbed {
  background-color: #eff1f4 !important;
  header {
    background-color: #4b4d52 !important;
  }
  .onglet {
    background: #eff1f4 !important;
  }
}
